<!--
    Component toolbar red
-->
<template>
  <div class="ToolBarRedComponent">
    <button
      class="ToolBarRedComponentButtons"
      v-if="baseController.isStateRead()"
      v-on:click="baseController.goListHandlerViewError()"
      type="button"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.goList')" />
      <i class="fas fa-reply"></i>
    </button>

    <button
      class="ToolBarRedComponentButtons"
      v-if="baseController.isStateRead()"
      v-on:click="baseController.goAddHandlerViewError()"
      type="button"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.goAdd')" />
      <i class="fas fa-plus"></i>
    </button>

    <button
      class="ToolBarRedComponentButtons"
      v-if="baseController.isStateRead()"
      v-on:click="baseController.goEditHandlerViewError()"
      type="button"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.goEdit')" />
      <i class="far fa-edit"></i>
    </button>

    <button
      class="ToolBarRedComponentButtons"
      v-if="baseController.isStateRead()"
      v-on:click="baseController.goDeleteWithHandlerViewError()"
      type="button"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.delete')" />
      <i class="fas fa-eraser"></i>
    </button>

    <button
      class="ToolBarRedComponentButtons"
      v-if="baseController.isStateRead()"
      v-on:click="baseController.goPositionWithHandlerViewError(0)"
      type="button"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.first')" />
      <i class="fas fa-fast-backward"></i>
    </button>

    <button
      class="ToolBarRedComponentButtons"
      v-if="baseController.isStateRead()"
      v-on:click="baseController.goPositionWithHandlerViewError(baseController.first + - 1)"
      type="button"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.prev')" />
      <i class="fas fa-step-backward"></i>
    </button>

    <button
      class="ToolBarRedComponentButtons"
      v-if="baseController.isStateRead()"
      v-on:click="baseController.goPositionWithHandlerViewError(baseController.first)"
      type="button"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.refresh')" />
      <i class="fas fa-sync-alt"></i>
    </button>

    <button
      class="ToolBarRedComponentButtons"
      v-if="baseController.isStateRead()"
      v-on:click="baseController.goPositionWithHandlerViewError(baseController.first + 1)"
      type="button"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.next')" />
      <i class="fas fa-step-forward"></i>
    </button>

    <button
      class="ToolBarRedComponentButtons"
      v-if="baseController.isStateRead()"
      v-on:click="baseController.goPositionWithHandlerViewError(baseController.totalRecords)"
      type="button"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.last')" />
      <i class="fas fa-fast-forward"></i>
    </button>

    <button
      class="ToolBarRedComponentButtons"
      v-if="baseController.isStateAdd() || baseController.isStateEdit()"
      type="button"
      v-on:click="baseController.saveWithHandlerViewError()"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.save')" />
      <i class="far fa-thumbs-up"></i>
    </button>

    <button
      class="ToolBarRedComponentButtons"
      v-if="baseController.isStateAdd() || baseController.isStateEdit()"
      v-on:click="baseController.cancelWithHandlerViewError();"
      type="button"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.cancel')" />
      <i class="far fa-thumbs-down"></i>
    </button>
  </div>
</template>
<script>
import BaseComponent from "../base/BaseComponent";
import ToolTipComponent from "../tooltip/ToolTipComponent";

export default {
  extends: BaseComponent,
  name: "ToolBarRedComponent",
  props: {
    baseController: {
      type: Object,
      default: null
    }
  },
  methods: {},
  components: {
    ToolTipComponent
  }
};
</script>
<style scoped>
.ToolBarRedComponent {
  width: 100%;
  float: left;
  clear: left;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.ToolBarRedComponentButtons,
.ToolBarRedComponentButtons:focus,
.ToolBarRedComponentButtons:active {
  width: 30px;
  height: 30px;
  color: var(--rf-toolbarsButtonFontColor, #466289);
  background-color: transparent;
  border: none;
  float: left;
  cursor: pointer;
  outline: none;
  font-size: 18px;
}
</style>